import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import {
  Flex,
  Box,
  Text,
  Image,
} from 'v-ui-elements';

import withTopics from '../../data/Topics';
import withResponsive from '../../hoc/withResponsive';

import LogoText from '../../components/LogoText';
import Fullpage from '../../components/Fullpage';

import PolygonPhoto from './PolygonPhoto';

class Home extends PureComponent {
  state = {
    current: 0,
  }

  render() {
    const { data, browser, ...props } = this.props;
    const { current } = this.state;
    const fund = data[current];

    const fundText = (
      <Box>
        <Flex
          flexDirection={['row', null, null, 'column']}
          alignItems={['center', null, null, 'flex-start']}
        >
          <Box w={['3em', null, null, '2em']} ml="0.25em" mr="1em" my="1em">
            <Image src={fund.category.icon} />
          </Box>
          <Box w={1}>
            <Text
              color={fund.category.color}
              f="1.75em"
            >
              {fund.title}
            </Text>
          </Box>
        </Flex>

        <Box w="6em" pb="0.25em" my="1em" bg="white" />
        <Text>{fund.short}</Text>
      </Box>
    );

    return (
      <Fullpage {...props}>
        <Flex
          w={1}
          px={['1em', null, '3em', null, '5em']}
          flexDirection={['column', null, null, 'row']}
        >
          <Box w={[1, null, null, 4 / 12]}>
            <LogoText
              mx={['2em', null, '4em', 0]}
              mb={['3em', null, null, 0]}
              transform={browser.greaterThan.medium && 'translateY(-50%)'}
            />
            {browser.greaterThan.medium && fundText}
          </Box>
          <Box w={[0, null, null, 2 / 12]} />
          <Box
            w={[1, null, null, 6 / 12]}
            px={['2em', null, '4em', 0]}
          >
            <PolygonPhoto src={fund.photo} category={fund.category} index={current + 1} />
          </Box>
          <Box w={[1, null, null, 0]}>
            {!browser.greaterThan.medium && fundText}
          </Box>
        </Flex>
      </Fullpage>
    );
  }
}

Home.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  browser: PropTypes.shape().isRequired,
};

export default compose(
  withTopics,
  withResponsive,
)(Home);



