import React, { PureComponent } from 'react';
import { Loader } from 'v-ui-elements';

export default (Comp) => {
  class WithTopics extends PureComponent {
    state = {
      data: null,
    }

    componentWillMount() {
      // 模擬非同步取得資料
      import('./data').then(this.handleDataLoaded);
    }

    handleDataLoaded = (data) => {
      this.setState({ data: data.default });
    }

    render() {
      const { data } = this.state;
      // 資料未取得，返回loading狀態
      if (!data) return <Loader />;
      return (
        <Comp
          {...this.props}
          data={data}
        />
      );
    }
  }

  return WithTopics;
};



