import { injectGlobal } from 'styled-components';
import { colors } from 'v-ui-elements';

/* eslint-disable no-unused-expressions */
injectGlobal`
  html,
  body {
    height: 100%;
    width: 100%;
  }

  body {
    background: ${colors.black};
  }
`;



