import React from 'react';
// import PropTypes from 'prop-types';

import {
  Image,
  Box,
  Text,
  Fixed,
  Flex,
  // Container,
} from 'v-ui-elements';

import logo from './logo.svg';
import config from '../../config';

const Header = () => {
  const showText = false;
  return (
    <Fixed top="0" left="0" right="0" z={99} bg="rgba(0, 0, 0, 0.75)">
      <Flex px="1.5em" align="center" height={config.headerHeight}>
        <Box>
          <Image height="2em" src={logo} />
        </Box>
        {showText && <Text>主題式投資平台</Text>}
      </Flex>
    </Fixed>
  );
};

Header.propTypes = {

};

export default Header;



