import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

import {
  Image,
  Box,
  Relative,
  Absolute,
  Text,
} from 'v-ui-elements';

const polygonOffset = 7;
const decoOffset = '2em';
const svgConfig = {
  width: 690,
  height: 480,
  offset: 3,
};

const polygonPoints = [
  [0, polygonOffset],
  [100, 0],
  [100, 100],
  [0, 100 - polygonOffset],
];

const polygonPointsInverted = [
  [0, 0],
  [100, polygonOffset],
  [100, 100 - polygonOffset],
  [0, 100],
];

const clipPathGen = points => points.map(point => point.map(p => `${p}%`).join(' ')).join();

const Clipping = Box.extend`
  clip-path: polygon(${clipPathGen(polygonPoints)});
`;

const ClippingInverted = Box.extend`
  clip-path: polygon(${clipPathGen(polygonPointsInverted)});
`;

const PolygonSVG = styled.svg`
  width: 100%;
  height: 100%;
  polygon {
    stroke: white;
    stroke-width: 1px;
    fill: none;
  }
`;

const PolygonPhoto = ({
  src,
  category: { color, text },
  index,
  ...props
}) => (
  <Box {...props}>
    <Relative>
      <Clipping>
        <Image src={src} />
      </Clipping>
      <Absolute
        top={`-${decoOffset}`}
        left={`-${decoOffset}`}
        bottom={decoOffset}
        right={decoOffset}
      >
        <PolygonSVG
          xmlns="http://www.w3.org/2000/svg"
          viewBox={`-${svgConfig.offset} -${svgConfig.offset} ${svgConfig.width + svgConfig.offset} ${svgConfig.height + svgConfig.offset}`}
        >
          <polygon
            points={polygonPoints.map(([w, h]) => [(w / 100) * svgConfig.width, (h / 100) * svgConfig.height].join()).join(' ')}
          />
        </PolygonSVG>
      </Absolute>
      <Absolute
        left="-4em"
        bottom="1rem"
        is={ClippingInverted}
        bg={color}
        color="black"
        p="1.5em"
        pl="2.5em"
      >
        <Text
          f="3em"
          align="right"
          fontWeight="300"
        >
          {(String(index)).padStart(2, 0)}
        </Text>
        <Text fontWeight="bold">{text}類</Text>
      </Absolute>
    </Relative>
  </Box>
);

PolygonPhoto.propTypes = {
  src: PropTypes.string.isRequired,
  category: PropTypes.shape({
    text: PropTypes.string,
    color: PropTypes.string,
  }).isRequired,
  index: PropTypes.number.isRequired,
};

export default PolygonPhoto;



